




import { Component, Prop, Watch, Vue } from 'vue-property-decorator'
import exp from '@/assets/ts/editor.explain'

@Component
export default class AceEditor extends Vue {
  @Prop()
  editorId !:string
  @Prop({default: false})
  readOnly !:boolean
  @Prop({default: exp})
  content !:string
  @Prop({default: "monokai"})
  theme !:string
  @Prop({default: "text"})
  mode !:string
  @Prop({default: 4})
  tabSize !:number
  @Prop({default: 14})
  fontSize !:number

  aceOptions :object = {
    maxLines: null, // 
    minLines: null, // 
    fontSize: this.fontSize, // 
    theme: "ace/theme/" + this.theme, // 
    mode: "ace/mode/" + this.mode, // 
    tabSize: this.tabSize, //  4 
    readOnly: this.readOnly, //
    enableSnippets: true, //
    enableLiveAutocompletion: true,
    enableBasicAutocompletion: true,
    value: this.content
  }

  editor :any

  @Watch('content')
  valueChange(val :string, oldVal :string) {
    this.editor.setValue(val)
    this.editor.moveCursorTo(0, 0)
  }
  //ace
  private initEditor() :void {
    let ace = require("ace-builds")
    require("ace-builds/webpack-resolver")
    require("ace-builds/src-noconflict/ext-language_tools")
    this.editor = ace.edit(this.editorId, this.aceOptions)
  }

  mounted() :void {
    this.initEditor()
  }

}
