




























import { Component, Prop, Watch, Vue } from 'vue-property-decorator'
import axios from 'axios'
import { sysConf } from '@/assets/ts/path.conf'

@Component
export default class Dialog extends Vue {
  private SLASH !:any
  @Prop({default: false}) isInContainer !:boolean // 
  @Prop() path !:string //
  @Prop() apiUrl !:string //API
  // @Prop({default: true}) isAnimated !:boolean //
  @Prop({default: true}) isOper !:boolean //
  @Prop({default: ''}) title !:string //

  isWindowIn :boolean = true //
  isBack :boolean = false //
  isNext :boolean = false //
  historyPaths :object[] = [] //
  currentLocation :number = -1 //
  isCancelClick :boolean = false  //cancel
  isOpenClick :boolean = false //open
  selectedFile :object = {type: '', path: ''} //
  tpath :string = this.path //
  dirSelectIndex :number = -1 //
  fileSelectIndex :number = -1 //
  fileList :object = {} //
  menuSelectIndex :number = -1 //
  menus :any = sysConf.menus //

  //currentLocation
  @Watch('currentLocation')
  valueChange(val :number, oldVal :number) {
    this.isBack = val > -1
    this.isNext = val < this.historyPaths.length -1
    if(val === -1) {
      this.tpath = this.path
      this.menuSelectIndex = -1
    } else {
      let history :any = this.historyPaths[val]
      this.tpath = history.path
      if(history.type === 'menu')
        this.menuSelectIndex = history.index
    }
    this.isOpenClick = false
    this.initWindowContentListHighlight()
    this.getFileTree()
  }

  //
  private getFileTree() :void {
    axios.post(this.apiUrl, {path: this.tpath}).then((res :any) => {
      this.fileList = res.data.data
    })
  }
  //
  private setHistoryPath(type :string, index :number, path :string) :void {
    if(this.historyPaths.length > this.currentLocation  + 1)
      this.historyPaths = this.historyPaths.slice(0, this.currentLocation + 1)
    this.historyPaths.push({type, index, path})
    this.currentLocation++
  }
  //
  private back() :void {
    this.currentLocation--
  }
  //
  private next() :void {
    this.currentLocation++
  }
  //
  private menuSelectUploadFileList(index :number) :void {
    if(this.menuSelectIndex === index) return
    this.setHistoryPath('menu', index, this.menus[index].path)
  }
  //
  private folderSelectHighlight(index :number, name :string) :void {
    this.fileSelectIndex = -1
    this.dirSelectIndex = index
    this.isOpenClick = true
    this.selectedFile = {type: 'folder', path: (this.tpath + name)}
  }
  //
  private fileSelectHighlight(index :number, name :string) :void {
    this.dirSelectIndex = -1
    this.fileSelectIndex = index
    this.isOpenClick = true
    this.selectedFile = {type: 'file', path: (this.tpath + name)}
  }
  //
  private openFolder(name :string) :void {
    let folderPath = this.tpath + name + this.SLASH
    this.setHistoryPath('folder', this.menuSelectIndex, folderPath)
  }
  //
  private openFile(name :string) :void {
    this.$emit("openFile", this.tpath, name)
  }
  //
  private cancel() :void {
    this.isWindowIn = false
    this.isCancelClick = true
    this.isOpenClick = false
    setTimeout(() => {
      this.$emit('closeEvent')
    },1000)
  }
  //
  private open() :void {
    this.$emit('openEvent', this.selectedFile)
    this.isWindowIn = false
    setTimeout(() => {
      this.$emit('closeEvent')
    },1000)
  }
  //
  private initWindowContentListHighlight() :void {
    this.dirSelectIndex = -1
    this.fileSelectIndex = -1
  }
  //
  private initWindowData() :void {
    this.tpath = this.path
    this.isWindowIn = true
    this.isCancelClick = false
    this.isOpenClick = false
    this.menuSelectIndex = -1
    this.initWindowContentListHighlight()
  }
  //
  mounted() :void {
    this.initWindowData()
    this.getFileTree()
  }
}
